/*------------------------------------*\
  #SECONDARY FONT
\*------------------------------------*/
@font-face {
    font-family: 'NeuzeitS-bold';
    src: local('sans-serif'), url('../fonts/NeuzeitS-Bold.otf') format('opentype'),
        url('../fonts/NeuzeitS-Bold.woff') format('woff'),
        url('../fonts/NeuzeitS-Bold.woff2') format('woff2');
    font-display: swap;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: normal;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/*------------------------------------*\
  #FONT ICONS
\*------------------------------------*/

@font-face {
    font-family: 'fontello';
    src: url('../fonts/fontello.eot?56083049');
    src: url('../fonts/fontello.eot?56083049#iefix') format('embedded-opentype'),
        url('../fonts/fontello.woff2?56083049') format('woff2'),
        url('../fonts/fontello.woff?56083049') format('woff'),
        url('../fonts/fontello.ttf?56083049') format('truetype'),
        url('../fonts/fontello.svg?56083049#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'fontello' !important;
    font-style: normal;
    font-weight: 900;
    font-variant: normal;
    text-transform: none;
    line-height: normal;
    font-size: 3.2rem;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

html,
body {
    font-family: @font-family--primary;
}

.font-weight-heavy {
    font-weight: 900;

    @-moz-document url-prefix() {
        font-weight: 400;
    }
}

.font-weight-bold {
    font-weight: 700;
    font-family: @font-family--secondary;

    @-moz-document url-prefix() {
        font-weight: 400;
    }
}

strong,
b {
    &:extend(.font-weight-bold);
}

.font {
    &-size {
        &--nano {
            font-size: @font-size--nano;
        }

        &--micro {
            font-size: @font-size--micro;
        }

        &--milli {
            font-size: @font-size--milli;
        }

        &--base {
            font-size: @font-size--base;
        }

        &--kilo {
            font-size: @font-size--kilo;
        }

        &--mega {
            font-size: @font-size--mega;
        }

        &--giga {
            font-size: @font-size--giga;
        }

        &--tera {
            font-size: @font-size--tera;
        }

        &--peta {
            font-size: @font-size--peta;
        }

        &--exa {
            font-size: @font-size--exa;
        }

    }

    &-family {
        &--primary {
            font-family: @font-family--primary;
        }

        &--secondary {
            font-family: @font-family--secondary;
        }
    }

    &--xl {
        .fluid-type(font-size, @screen__xxs, @screen__xl, @font-size--tera, @font-size--exa);
        font-family: @font-family--secondary;
    }
}

h1 {
    .fluid-type(font-size, @screen__xxs, @screen__xl, @font-size--tera, @font-size--peta);
    font-family: @font-family--secondary;
}

h2 {
    .fluid-type(font-size, @screen__xxs, @screen__xl, @font-size--giga, @font-size--tera);
    font-family: @font-family--secondary;
}

h3 {
    font-size: @font-size--tera;
    font-family: @font-family--secondary;
}

h4 {
    .fluid-type(font-size, @screen__xxs, @screen__xl, @font-size--mega, @font-size--giga);
    font-family: @font-family--secondary;
}

.color-black {
    color: @color-black;
}

.color-white {
    color: @color-white;
}

.color-platinum {
    color: @color-platinum;
}

/*------------------------------------*\
  #PRIMARY FONT
\*------------------------------------*/

@font-face {
    font-family: 'NeuzeitS';
    src: local('sans-serif'), url('../fonts/NeuzeitS-Regular.ttf') format('truetype'),
